import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { View, ScreenSpinner, AdaptivityProvider,Avatar,Snackbar,FormStatus,ActionSheet,Div,Alert,FormItem, Input,RichCell,ActionSheetItem, AppRoot } from '@vkontakte/vkui';
import { Icon28ChecksOutline } from '@vkontakte/icons';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import ErrorPage from './panels/errorPage';
import Loading from './panels/loading';
import Persik from './panels/Persik';

var wsConnection  = new WebSocket("wss://freevkstickers.ru:9000");
const App = () => {
	const [activePanel, setActivePanel] = useState('loading');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);
	const [Score, setScore] = useState(0);
	const [Online, setOnline] = useState(10);
	const [Fact, setFact] = useState("Загружаем факты, чтобы тебе было не скучно...");
	const [Theme, setTheme] = useState("white");
	const [Erorr, setErorr] = useState("Ошибка подключения к серверу. Попробуй зайти чуть позже.");

	useEffect(() => {

		bridge.subscribe(({ detail: { type, data }}) => {
	    if (type === 'VKWebAppUpdateConfig') {
	      const schemeAttribute = document.createAttribute('scheme');
	      schemeAttribute.value = data.scheme ? data.scheme : 'bright_light';
	      setTheme(data.scheme !== "bright_light" ? 'black' : 'white')
	      document.body.attributes.setNamedItem(schemeAttribute);
	    }
	  });

		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null)
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

  async function registration(){
		const user = await bridge.send('VKWebAppGetUserInfo');
		wsConnection.send(JSON.stringify({event: 'registration', params: window.location.search, user: user}));
	}

	wsConnection.onopen = async function(message) {
	 const token = await bridge.send("VKWebAppGetAuthToken", {"app_id": 7966180, "scope": ""});
	 if(token.access_token){
	   registration();
    }
	};

	wsConnection.onerror = async function(message) {
		setErorr("Ошибка подключения к серверу. Попробуй зайти чуть позже.")
 	  setActivePanel('errorPage')
		setPopout(null)
	};

	wsConnection.onclose = async function(message) {
		setErorr("Ошибка подключения к серверу. Попробуй зайти чуть позже.")
 	  setActivePanel('errorPage')
		setPopout(null)
	};

	wsConnection.onmessage = function(message) {
	try{
	var json = JSON.parse(message.data);
	switch (json.event) {
		case "setscore" :
		 setScore(+Score + +json.value)
		 break;
		 case "fact" :
		 setFact(json.value)
		 break;
		 case "online" :
		 setOnline(json.value)
		 break;
		 case "error" :
		 setErorr(json.value)
		 console.log(json.value)
		 setActivePanel('errorPage')
		 setPopout(null)
		 break;
		 case "transfercomplite" :
 		 setScore(json.value)
		 setPopout(<Snackbar before={<Icon28ChecksOutline fill={Theme === 'black' ? 'white' : 'black'} />} duration={1300} onClose={() => setPopout(null)}>Перевод выполнен.</Snackbar>)
 		 break;
		 case "transferalert":
		 setPopout(
			 <Alert
        actions={[{
          title: 'Закрыть',
          mode: 'destructive',
          autoclose: true
        }]}
        actionsLayout="vertical"
        onClose={() => setPopout(null)}
        header="Новый перевод"
        text={`${json.about.first_name} перевел вам ${json.summa} секунд`}
      />
		 )
		 break;
		 case "good":
		 setActivePanel('home')
		 setPopout(null)
		 if(window.location.hash.indexOf('#t') > -1){
			 var id = window.location.hash.replace(/[^+\d]/g, '')
			   async function GetUser(){
					 const allparams = btoa(window.location.search)
					 const response = await fetch('https://h154851.srv11.test-hf.su/void/api.php?action=getuser&key=' + allparams + '&id=' + id);
			     const json = await response.json()
					 if(json.status == "good"){
						 setPopout(
							 <ActionSheet onClose={() => setPopout(null)}>
							 <FormStatus header="Ошибка" mode="error" id="transferalert" style={{display: 'none', color: 'red', marginRight: 8}}>
							 </FormStatus>
							 <RichCell before={<Avatar size={48} src={json.response.photo_200} />} caption="Получит ваши секунды">
				 			   {json.response.first_name} {json.response.last_name}
				 		   </RichCell>
				 		   <FormItem top="Сумма">
				         <Input id="summa" type="number" />
				       </FormItem>
				 			  <ActionSheetItem onClick={() => {
									var summa = document.getElementById('summa').value
									wsConnection.send(JSON.stringify({event: 'transfer', params: window.location.search, toid: id, summa: summa}));
								}}>
				 				Перевести
				 		   	</ActionSheetItem>
				        <ActionSheetItem autoclose mode="destructive">
				           Закрыть
				         </ActionSheetItem>
				       </ActionSheet>
						 )
					 }
				 }GetUser()
		 }
		 break;
		 case 'errortransfer':
		 try{
			 document.getElementById('transferalert').style.display = 'block'
			 document.getElementById('transferalert').innerHTML = json.message
		 }catch(e){

		 }
		 break;
	}
	}catch(e){
	console.log('Ошибка ' + e);
	}
	};
	return (
		<AdaptivityProvider>
			<AppRoot>
				<View activePanel={activePanel} popout={popout}>
					<Home id='home' fetchedUser={fetchedUser} go={go} Online={Online} setPopout={setPopout} Score={Score} Fact={Fact}/>
					<ErrorPage id='errorPage' Erorr={Erorr}/>
					<Loading id='loading' />
					<Persik id='persik' go={go} />
				</View>
			</AppRoot>
		</AdaptivityProvider>
	);
}

export default App;
