import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

import { Panel, PanelHeader, Header, Button, SubnavigationButton, Subhead,FormStatus,ActionSheet,ActionSheetItem, Title, SimpleCell, FixedLayout, SubnavigationBar, CardGrid, Card, Group, Cell, Div, Avatar } from '@vkontakte/vkui';
import ParticleEffectButton from 'react-particle-effect-button'
import './Persik.css';
import anime from 'animejs/lib/anime.es.js';
import mainbutton from '../img/mainbutton.png';
import bridge from '@vkontakte/vk-bridge';
import { Icon56DonateOutline,Icon56MoneyTransferOutline,Icon56ArticleOutline,Icon24Poll,Icon28CopyOutline,Icon24MoneyTransferOutline } from '@vkontakte/icons';

const Home = ({ id, go, fetchedUser, Score, Fact, setPopout, Online }) => {
  const [Theme, setTheme] = useState("white");

  bridge.subscribe(({ detail: { type, data }}) => {
    if (type === 'VKWebAppUpdateConfig') {
      const schemeAttribute = document.createAttribute('scheme');
      schemeAttribute.value = data.scheme ? data.scheme : 'bright_light';
      setTheme(data.scheme !== "bright_light" ? 'black' : 'white')
      document.body.attributes.setNamedItem(schemeAttribute);
    }
  });

  function declOfNum(number, titles) {
    var cases = [2, 0, 1, 1, 1, 2];
    return titles[ (number%100>4 && number%100<20)? 2 : cases[(number%10<5)?number%10:5] ];
   }


	return(
	<Panel id={id}>
  <div className="rootPanel">
		<PanelHeader >Главная</PanelHeader>
		<Group>
    <div className="onlineblock" style={{backgroundColor: Theme === 'black' ? 'white' : 'black'}}>
      <center><a style={{color: Theme}}>Онлайн: {Online}</a></center>
    </div>
    <center><div className="mainblock">
     <h1 className="score" id="score">{Score.toFixed(0)}</h1>
     <Subhead weight="regular" style={{marginTop: -80}}>{declOfNum(Score, ['секунда', 'секунды', 'секунд'])}</Subhead>
    </div></center>
    <Div>
      <center>{Fact}</center>
    </Div>
		</Group>
    {fetchedUser &&
    <FixedLayout vertical="bottom" style={{marginBottom: 16}}>
    <Div style={{display: 'flex'}}>
       <Button size="s" stretched mode="secondary" before={<Icon24Poll fill={Theme === 'black' ? 'white' : 'black'} />} style={{ marginRight: 8, color: Theme === 'black' ? 'white' : 'black'}}> Топ </Button>
       <Button size="s" stretched mode="secondary" before={<Icon24MoneyTransferOutline fill={Theme === 'black' ? 'white' : 'black'} />} style={{ color: Theme === 'black' ? 'white' : 'black'}} onClick={() => {
         setPopout(
           <ActionSheet onClose={() => setPopout(null)}>
           <Div>
           <FormStatus header="Ссылка на перевод" mode="default">
             Твоя ссылка на перевод, перейдя по ней, игрок сможет перевести тебе секунды... Хотя.. Зачем они тебе?
            </FormStatus>
            </Div>
            <SimpleCell disabled after={<Icon28CopyOutline fill={Theme === 'black' ? 'white' : 'black'} onClick={async () => {
              const response = await bridge.send("VKWebAppCopyText", {"text": "vk.com/app7966180#t" + fetchedUser.id});
              if(response.result == true){
                bridge.send("VKWebAppTapticNotificationOccurred", {"type": "success"});
              }else{
                bridge.send("VKWebAppTapticNotificationOccurred", {"type": "error"});
              }
            }}/>}>vk.com/app7966180#t{fetchedUser.id}</SimpleCell>
            <ActionSheetItem autoclose mode="destructive">
               Закрыть
             </ActionSheetItem>
           </ActionSheet>
         )
       }}> Перевод </Button>
    </Div>
    </FixedLayout>}
  </div>
	</Panel>
 )
};

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
